<template>
    <el-main>
        <el-form :model="LoginForm" ref="LoginForm" :rules="rule" label-width="0" class="login-form">
            <h3>Áî®Êà∑ÁôªÂΩï</h3>
            <el-form-item prop="username" label="Áî®Êà∑ÂêçÔºö" label-width="80px" >
              <el-col>
                <el-input type="text" v-model="LoginForm.username" placeholder="Áî®Êà∑Âêç" >
                </el-input>
              </el-col>
            </el-form-item>
            <el-form-item prop="password" label="ÂØÜÁ†ÅÔºö" label-width="80px">
              <el-col>
                <el-input type="password" v-model="LoginForm.password" placeholder="ÂØÜ Á†Å" >
                </el-input>
              </el-col>
            </el-form-item>
            <el-form-item >
              <el-col>
                <el-button type="danger" class="submitBtn" round @click.native.prevent="submit" v-loading.fullscreen.lock="fullscreenLoading"> ÁôªÂΩï </el-button>
                <el-button type="primary" class="resetBtn" round @click.native.prevent="reset"> ÈáçÁΩÆ </el-button> 
              </el-col>
                <hr> 
                <p>ÂøòËÆ∞ÂØÜÁ†ÅÔºåËØ∑ËÅîÁ≥ªË∂ÖÁ∫ßÁÆ°ÁêÜÂëò<span class="to" @click="toregin">Ë∂ÖÁ∫ß</span></p>
              
            </el-form-item>
        </el-form>
    </el-main>
</template>
<script>
// import axios from 'axios';
import store from '@/vuex/store';
export default { 
    // .... 
    data () { 
        return { 
            fullscreenLoading: false,
            LoginForm: { 
                username: '', 
                password: '' 
            }, 
            // logining: false, 
            rule: { 
                username:[{
                    required:true,
                    min: 3,
                    max: 10,
                    message:'Áî®Êà∑ÂêçÊòØÂøÖÈ°ªÁöÑÔºåÈïøÂ∫¶‰∏∫3-10‰Ωç',
                    trigger: 'blur'
                } ], 
                password: [ { 
                    required: true, 
                    message: 'ÂØÜÁ†ÅÊòØÂøÖÈ°ªÁöÑÔºÅ', 
                    trigger: 'blur' 
                } ] 
            } 
        } 
    }, 
    methods: { 
        // ... 
        submit () { 
            this.fullscreenLoading = true;
            this.$refs.LoginForm.validate(valid => { 
                if (valid) { 
                    //Ëé∑ÂèñÂÄº  
                    var name = this.LoginForm.username;  
                    var password = this.LoginForm.password;  
                    if(name == '' || password == ''){  
                    this.$message({  
                        message : 'Ë¥¶Âè∑ÊàñÂØÜÁ†Å‰∏∫Á©∫ÔºÅ',  
                        type : 'error'  
                    })  
                    return;  
                    } else{
                    this.fullscreenLoading = true;
                    setTimeout(() => {
                        this.fullscreenLoading = false;
                        let flag = false;
                        this.$axios.post("http://localhost:8080/login.action",{
                          name:this.LoginForm.username,
                          password:this.LoginForm.password
                        })
                        .then(response=>{
                        let errorcode = response.data.head.errorCode;
                        if(errorcode == '100004'){
                          this.$router.push('resetPassword');
                          return;
                        }
                        if(errorcode != '000000'){
                          let errorMessage = response.data.head.errorMessage;
                          this.$message({
                            message: errorMessage,
                            type: 'error'
                          });
                          return;
                        }
                        store.commit('initOptions',response.data.body.branch_rate);
                        store.commit('initWorker',response.data.body.worker);
                        this.$message({
                            message: 'ÊÅ≠Âñú‰Ω†ÔºåÁôªÂΩïÊàêÂäü',
                            type: 'success'
                        });
                        flag=true;
                        this.$router.push('/container');
                        return;
                        }).catch(error=>{
                        console.log('error='+error);
                        this.$message({
                            message: 'ÁôªÂΩïÂ§±Ë¥•',
                            type: 'error'
                        });
                        flag=false;
                        this.$router.push('/');
                        return;
                        });
                        //Ë∂ÖÊó∂ÂêéÁöÑÂ§ÑÁêÜ
                        if(flag){
                        this.$router.push('/container');
                        }else{
                        this.$router.push('/');
                        }

                    }, 2000);
                    
                    }
                } else { 
                    console.log('submit err') 
                } 
            }) 
        }, 
        reset () { 
            this.$refs.LoginForm.resetFields() 
        }, 
        toregin () { 
            this.$router.push('/regin') 
        } 
    }
}
</script>
<style scoped>
    .login-form {
        margin: 20px auto;
        width: 310px;
        background: #fff;
        box-shadow: 0 0 35px #b4bccc;
        padding: 30px 30px 0 30px;
        border-radius: 25px;
        /* background:url('../assets/logo.png') no-repeat;
        background-size:100%;
        -moz-background-size:100% 100%; */
    }
    .submitBtn {
      width: 70%;
    }
    

    .to {
      color: #67c23a;
      cursor: pointer;
    }
</style>


